import './styles/app.scss';

function getUsers() {
    let url = 'https://itrex-react-lab-files.s3.eu-central-1.amazonaws.com/react-test-api.json';
    return fetch(url)
    .then((resp) => resp.json())
    .then((resp) => {
        for(let user of resp) {
            console.log(user);
            let row = document.createElement("tr");
            let tdId = document.createElement("td");
            tdId.textContent = user.id;
            row.append(tdId);
            let tdFirstName = document.createElement("td");
            tdFirstName.textContent = user.firstName;
            row.append(tdFirstName);
            let tdLastName = document.createElement("td");
            tdLastName.textContent = user.lastName;
            row.append(tdLastName);
            let tdEmail = document.createElement("td");
            tdEmail.textContent = user.email;
            row.append(tdEmail);
            let tdPhone = document.createElement("td");
            tdPhone.textContent = user.phone;
            row.append(tdPhone);
            let tdState = document.createElement("td");
            tdState.textContent = user.adress.state;
            row.append(tdState);

            document.getElementById("table").append(row);
        }
    })
    .catch(err => alert('Failed to load users'));
}

getUsers();
